import React from "react";
import ReactDOM from "react-dom";
import Modal from "react-responsive-modal";
import SWCard from "./components/SWCard";
import Wrapper from "./components/Wrapper";
import sw1 from "./sw1.json";
import sw2 from "./sw2.json";
import sw3 from "./sw3.json";
import sw4 from "./sw4.json";
import cloneslvl1 from "./cloneslvl1.json";
import cloneslvl2 from "./cloneslvl2.json";
import cloneslvl3 from "./cloneslvl3.json";
import cloneslvl4 from "./cloneslvl4.json";
import "./App.css";

const sw = [sw1, sw2, sw3, sw4, cloneslvl1, cloneslvl2, cloneslvl3, cloneslvl4];

const shuffle = random => {
  for (let j = random.length - 1; j > 0; j--) {
    const randomizer = Math.floor(Math.random() * (j + 1));
    [random[j], random[randomizer]] = [random[randomizer], random[j]];
  }
  return random;
};

let i = 0;
let newSW = shuffle(sw[i]);
let modalText;
let right;
let reset;
let win;
let lose;
let levelReset;
let levelDown;
let levelUp;

class App extends React.Component {
  state = {
    swchars: newSW,
    chosen: [],
    count: 0,
    open: false,

  };

  levelReset = () => 
    this.setState({
      chosen: [],
      count: 0,
      swchars: newSW
    });

  levelDown = () => {
    i === 0 ? i=0 : i--;
    newSW = shuffle(sw[i]);
    this.levelReset();
  };

  levelUp = () => {
    i === 7 ? i=7: i++;
    newSW = shuffle(sw[i]);
    this.levelReset();
  };

  onOpenModal = () => {
    this.setState({ open: true });
  };

  onCloseModal = () => {
    this.setState({ open: false });
  };

  select = id => {
    right = () =>
      this.setState({
        chosen: [
          ...this.state.chosen,
          this.state.swchars.find(sw => sw.id === id)
        ],
        count: parseFloat(this.state.count) + 1
      });

    reset = () =>
      this.setState({
        chosen: [],
        count: 0,
        swchars: newSW
      });

    win = () => {
      i++;
      if (i === 8) {
        i = 0;
        newSW = shuffle(sw[i]);
        reset();
        modalText = "You won! Try again?";
        this.onOpenModal();
      } else {
        if (i === 4) {
          newSW = shuffle(sw[i]);
          reset();
          modalText = "Ready for hardmode?";
          this.onOpenModal();
        }
        newSW = shuffle(sw[i]);
        reset();
      }
    };

    lose = () => {
      i = 0;
      newSW = shuffle(sw[i]);
      reset();
      modalText = "You lose.";
      this.onOpenModal();
    };

    newSW = shuffle(newSW);
    if (!this.state.chosen.find(sw => sw.id === id)) {
      right();
      if (this.state.count === this.state.swchars.length - 1) {
        win();
      } else {
        this.setState({
          swchars: newSW
        });
      }
    } else {
      lose();
    }
  };

  render() {
    const { open } = this.state;
    return (
      <>
        <Modal
          open={open}
          classNames={{ modal: "modalStyle" }}
          onClose={this.onCloseModal}
          center
        >
          <h2 className="modalText">{modalText}</h2>
        </Modal>
        <button className="levelDown" onClick={() => this.levelDown()}>
          Level Down
        </button>
        <button className="levelUp" onClick={() => this.levelUp()}>
          Level Up
        </button>
        <h1 className="score">
          {this.state.count} / {this.state.swchars.length} <br />
          Right
        </h1>
        <h1 className="level">
          Level <br /> {parseFloat(i) + 1}
        </h1>
        <h1 className="title">
          Star
          <br />
          Wars
          <br />
          Clicky
          <br />
          Game
        </h1>
        <h1 className="game">
          Don't
          <br />
          click
          <br />
          anything
          <br />
          twice
        </h1>
        <Wrapper>
          {this.state.swchars.map(swchar => (
            <SWCard
              key={swchar.name}
              id={swchar.id}
              image={swchar.image}
              select={this.select}
            />
          ))}
        </Wrapper>
      </>
    );
  }
}

export default App;
